html {
	height: 100%;

}

body {
	overflow: hidden;
	height: inherit;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering: optimizeLegibility;
}

#draw {
	z-index: 1;
}

#pseudoCanvas {
	z-index: 3;
	position: fixed;
}

.App {
	text-align: center;
}

input[type=color] {
	border: none;
}

.cursor {
	display: none;
	margin: auto;
	width: 16px;
	height: 16px;
	background: white;
	border-radius: 50%;
	z-index: 5;
	pointer-events: none;
	position: fixed;
	opacity: .85;
	border: 1px solid #000000;
}

canvas {
	cursor: none;
}

.border-x-none {
	border-top: 0;
	border-bottom: 0;
}

.icon {
	background-color: #e6e6e6 !important;
	background-position: center; 
	background-size: 20px 20px;
	background-repeat: no-repeat;
}

.pipette {
	background-image: url(/WebPaint/static/media/pipette.18863c6d.png);
}

.eraser {
	background-image: url(/WebPaint/static/media/eraser.83122748.png);
}

.selection {
	background-image: url(/WebPaint/static/media/selection.33b8977b.png);
}

.pencil {
	background-image: url(/WebPaint/static/media/pencil.4bf9902f.svg);
}

.upload {
	background-image: url(/WebPaint/static/media/upload.b64b9dd6.svg);
}

.trash {
	background-image: url(/WebPaint/static/media/trash.42275990.svg);
}

.paint-bucket {
	background-image: url(/WebPaint/static/media/paint-bucket.64b8acb7.svg);
}

.download {
	background-image: url(/WebPaint/static/media/download.99cf5f29.svg);
}

.storage {
	background-image: url(/WebPaint/static/media/database.9a119c34.svg);
}

.cloud-upload {
	background-image: url(/WebPaint/static/media/cloud-upload.31fb67d3.svg);
}

.close {
	background-image: url(/WebPaint/static/media/close.583a3ad4.svg);
}

.check {
	background-image: url(/WebPaint/static/media/check.bb193471.svg);
}

.forward {
	background-image: url(/WebPaint/static/media/forward.12edc81d.svg);
}

.reply {
	background-image: url(/WebPaint/static/media/reply.569df7fa.svg);
}

.info {
	background-image: url(/WebPaint/static/media/info.b44836c0.svg);
}

.storageModal {
	z-index: 1000;
}

.image-border {
	border: 1px solid #c2c7ce;
}

.active::after {
	content: "\21D1"
}

.mobile-active > div {
	list-style-type: none;
}

.mobile-active > div::before {
	padding-right: 15px;
	content: "\21D2";
}

.padding {
	padding: 30px !important;
}

.uk-nav-default {
	font-size: 1.25rem;
}

@media screen and (max-width: 800px) {
	input[type=color]{
		margin-top: 5%;
		margin-left: auto;
		margin-right: auto;
	}

	.thickness > select {
		margin-top: 5%;
	}

	#info-box > ul {
		margin: 0;
		padding: 0;
	}

	.info-li {
		display: flex;
		flex-wrap: wrap;
		flex-flow: column;
		justify-content: center;
		margin-bottom: 3vh !important;
		padding-bottom: 5px;
		border-bottom: 1px solid #666;
	}

	.info-li > .icon {
		margin: auto !important;
	}

	.info-li > .info-text {
		margin: 0;
		text-align: center;
	}

	.image-button-group {
		display: flex;
		flex-flow: column;
	}

	.image-button-group .uk-button {
		margin: 0 !important;
	}

	.ReactModal__Content.ReactModal__Content--after-open {
		top: 0px !important;
		left: 0px !important;
		right: 0px !important;
		bottom: 0px !important;
		border-radius: 0 !important;
	}
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

